html {
  height: 100%;
}

body {
  position: relative;
  min-width: 320px;
  font-family: opsans-400, sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 5px;
}

body::-webkit-scrollbar-track {
  width: 5px;
  background-color: transparent;
}

body::-webkit-scrollbar-thumb {
  background-color: #036dc1;
}

body.is-fixed {
  overflow: hidden;
}

h1.h1, h2.h2, h3.h3, h4.h4, h5.h5 {
  margin-top: 0;
}

img, .img-responsive {
  display: block;
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

section {
  padding: 50px 0 60px;
  background-color: #fff;
  position: relative;
}

section::after {
  content: '';
  display: block;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 18px 20px 0 20px;
  border-color: #fff transparent transparent transparent;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -18px;
  z-index: 99;
}

section:last-child::after {
  display: none;
}

section.bg--dark {
  background: #232323 url("../img/general/footer-bg.jpg") no-repeat;
}

section.bg--dark::after {
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 18px 20px 0 20px;
  border-color: #232323 transparent transparent transparent;
}

input {
  outline: none;
}

input:-webkit-autofill {
  -webkit-box-shadow: inset 0 0 0 50px #232323 !important;
  -webkit-text-fill-color: #fff !important;
  color: #fff !important;
}

.button {
  display: block;
  position: relative;
  width: 240px;
  height: 50px;
  line-height: 50px;
