  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-image: linear-gradient(to right, #7919dd, #9614db 33%, #c40cd8 66%);
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 0;
  transition: all 0.35s ease;
}

