.bg--dark {
	color: #fff;
	.s-title {
		color: #fff;
		&__title {
			font-family: oswald-400, sans-serif; }
		&__desc {
			font-family: $f;
			color: rgba(#fff, .7); } } }

.rub {
	font-family: rouble;
	font-style: normal; }

.num {
	font-family: $f-num; }

.color {
	&-green {
		color: #01C854; }
	&-blue {
		color: $color-main--light; } }

.windows {
	display: none; }

.wrapper {
	position: relative;
	min-height: 100vh;
	&__in {
		position: relative;
		z-index: 2;
		@include rmin(1024) {
			box-shadow: 0 1px 20px #000; } } }

.container {
	max-width: 1200px;
	margin: 0 auto;
	@include r(1200) {
		padding: 0 10px; } }

//s-title
.s-title {
	display: block;
	margin-bottom: 50px;
	text-align: center;
	color: #000;
	@include r(480) {
		margin-bottom: 30px; }
	&__title {
		text-transform: uppercase;
		font-family: oswald-500, sans-serif;
		letter-spacing: 1px; }
	&__desc {
		margin-top: 15px;
		font-family: $f-light;
		color: rgba(#000, .7); } }

//policy
.policy {
	margin-top: 20px;
	font-family: $f-light;
	font-size: 12px;
	color: #777;
	a {
		color: $color-main--light; } }

//sale
.sale {
	padding: 80px 0 90px;
	text-align: center;
	@include rmin(1024) {
		@include cover; }
	.s-title {
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 0;
		margin-right: 60px;
		color: #fff;
		position: relative;
		@include r(480) {
			margin-right: 0;
			margin-bottom: 30px; }
		@include r(375) {
			font-size: 23px; }
		@include r(320) {
			font-size: 20px; }
		&__title {
			span {
				color: #ff8c00; } } }
	&__btn {
		display: inline-block;
		vertical-align: middle;
		&::before {
			content: '\f295';
			font-family: fontawesome;
			font-size: 15px;
			color: #fff;
			margin-right: 15px; } } }

//whywe
.whywe {
	&__items {
		text-align: center;
		@include row-flex(); }
	//.whywe__item
	&__item {
		display: inline-block;
		padding-top: 150px;
		text-transform: uppercase;
		font-family: opsans-700, sans-serif;
		font-size: 15px;
		position: relative;
		@include col();
		@include size(3);
		@include size-xs(12);
		@include r(480) {
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0; } }
		&::before {
			content: '';
			display: block;
			@include vx;
			top: -0; }
		@each $item in sale, bonus, partner, garantee {
			&_#{$item} {
				&::before {
					@include s(#{$item}); } } } } }

//work
.work {
	background-color: #fff;
	&__items {
		text-align: center;
		@include row-flex(); }
	&__item {
		display: inline-block;
		padding-top: 150px;
		text-transform: uppercase;
		font-family: opsans-700, sans-serif;
		font-size: 15px;
		position: relative;
		@include col();
		@include size(3);
		@include size-xs(6);
		&::before {
			content: '';
			display: block;
			@include vx;
			top: -0; }
		&::after {
			content: '';
			display: block;
			@include vy;
			right: -80px;
			@include s('arrow-long');
			@include r(768) {
				display: none; } }
		&:last-child {
			&::after {
				display: none; } }
		&_order {
			&::before {
				@include s('call'); } }
		&_consalting {
			&::before {
				@include s('consultation'); } }
		&_pay {
			&::before {
				@include s('pay'); } }
		&_send {
			&::before {
				@include s('send'); } } } }

//payment
.payment {
	&__items {
		text-align: center;
		@include row-flex; }
	&__item {
		display: inline-block;
		background-color: #fff;
		@include tr;
		@include col;
		@include size(3);
		@include size-sm(6);
		@include size-xs(12);
		&:hover {
			box-shadow: 0 10px 50px -5px rgba(0,0,9,0.1); } }

	.icon-pay-tinkoff {
		width: 185px; } }
