//Buttons
.button {
	display: block;
	position: relative;
	width: 240px;
	height: 50px;
	line-height: 50px;

	border-radius: 27px;
	border: none;
	outline: none;

	color: #fff;
	font-family: $f-bold;
	font-size: 16px;
	letter-spacing: 0.05em;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	&:hover {
		text-decoration: none; }

	@include r(480) {
		@include gradient;
		&:hover {
			box-shadow: 0 4px 10px rgba($color-main--light, .7); } }

	&__inner {
		position: relative;
		z-index: 2; }

	canvas {
		background-color: transparent;
		position: absolute;
		top: -50px;
		right: -50px;
		bottom: -50px;
		left: -50px;
		z-index: 1; }

	&_gr {
		@include gradient;
		@include tr;
		&:hover {
			box-shadow: 0 4px 10px rgba($color-main--light, .7); } } }
